.stars {
  display: flex;
    
  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url(/images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
    
    &:nth-child(-n + 4) {
      background-image: url(/images/star-active.svg);
    }
  }
}
